.vetskitchen-inclusions {
  position: relative;
  padding-top: 15px;
  color: #000000;
  text-align: center;
}
.vetskitchen-inclusions img {
  max-width: 100%;
  height: auto;
}
.vetskitchen-inclusions__bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.vetskitchen-inclusions__wrapper {
  position: relative;
  z-index: 1;
}
.vetskitchen-inclusions h2 {
  margin-bottom: 50px;
  font-weight: 700;
  color: #018077;
  text-align: center;
}
.vetskitchen-inclusions__blocks {
  display: flex;
  justify-content: space-between;
}
.vetskitchen-inclusions__blocks > .plus-icon {
  margin-top: 42px;
}
@media screen and (max-width: 1199px) {
  .vetskitchen-inclusions__blocks > .plus-icon svg {
    width: 2vw;
  }
}
.vetskitchen-inclusions__item {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}
.vetskitchen-inclusions__item .title {
  padding-top: 35px;
  margin: 0 auto;
  max-width: 140px;
  font-size: 20px;
  font-weight: 700;
  line-height: 1.2;
}
.vetskitchen-inclusions__item .subtext {
  margin-top: 10px;
  font-size: 12px;
  font-weight: 600;
}
.vetskitchen-inclusions__item .plus-icon {
  position: absolute;
  top: auto;
  left: 0;
}
.vetskitchen-inclusions__item .item-wrap {
  position: relative;
}
.vetskitchen-inclusions__item .item-wrap:after {
  content: "";
  position: absolute;
  bottom: -27px;
  left: 50%;
  width: 100px;
  height: 10px;
  background: url("../../assets/images/light-border.png") no-repeat;
  background-size: 100%;
  transform: translate(-50%, 0);
}
.vetskitchen-inclusions__bottom {
  display: flex;
  justify-content: center;
  align-items: center;
  grid-gap: 30px;
  padding-top: 33px;
  width: 100%;
}
.vetskitchen-inclusions__bottom .text {
  max-width: 221px;
  font-size: 16px;
  font-weight: 500;
  line-height: 23px;
}
@media screen and (max-width: 767px) {
  .vetskitchen-inclusions {
    text-align: left;
  }
  .vetskitchen-inclusions__blocks {
    flex-direction: column;
    padding: 0px 15px;
  }
  .vetskitchen-inclusions .plus-icon svg {
    max-width: 30px;
    height: 30px;
  }
  .vetskitchen-inclusions__item {
    display: grid;
    grid-template-columns: 8fr 1fr;
    padding-left: 30px;
  }
  .vetskitchen-inclusions__item img {
    max-width: 70px;
  }
  .vetskitchen-inclusions__item .item-wrap {
    display: flex;
    align-items: center;
  }
  .vetskitchen-inclusions__item .item-wrap:after {
    display: none;
  }
  .vetskitchen-inclusions__item .title {
    padding-top: 0;
    max-width: 100%;
    font-size: 18px;
  }
  .vetskitchen-inclusions__bottom {
    padding: 20px 15px;
    justify-content: flex-start;
    grid-gap: 10px;
  }
  .vetskitchen-inclusions__bottom img {
    display: none;
  }
}